@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&family=Space+Mono:wght@400;700&display=swap');


* {
  font-family: 'Space Mono', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

p {
  cursor: pointer;
}

body {
  background-image: url(/src/img/bright-star-planet-galaxy-nebula-rockets.jpg);
  display: flex;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  background-color: #1d242f;
  
}

.App {
  text-align: center;
}

h1 {
  color: #fff;
  margin-bottom: 0.5rem;
  font-size: 1.75rem;
}

.TodoWrapper {
  background: hsla(240, 39%, 18%, 0.302);
  margin-top: 10rem;
  padding: 2rem;
  background: rgba( 29, 36, 47, 0.35 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 5px );
  -webkit-backdrop-filter: blur( 5px );
  border: 1px solid #482e8a;
}

.icon {
  cursor: pointer;
}

.TodoForm {
  width: 100%;
  margin-bottom: 1rem;
}

.todo-input {
  outline: none;
  background: none;
  border: 1px solid #482e8a;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 300px;
  color: #fff;
}

.todo-input::placeholder {
  color: #ffffff4d;
}

.todo-btn {
  background: transparent;
  color: #fff;
  border: 1px solid #482e8a;
  padding: 0.5rem;
  cursor: pointer;
  transition: 0.2s ease;
}

.todo-btn:active {
  color: #fff;
  background-color: #c5aeff;
  box-shadow: inset 4px 4px 12px #3d2775,
  inset -4px -4px 12px #53359f;
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: transparent;
  color: #fff;
  padding: 0.75rem 1rem;
  border: 2px solid #482e8a;
  /* border-radius: 10px; */
  margin-bottom: 1rem;
}

.fa-trash {
  margin-left: 0.75rem;
}

.incompleted {
  color: #fff;
  cursor: pointer;
}

.completed {
  color: #c5aeff;
  text-decoration: line-through;
  cursor: pointer;
}

.edit-icon,
.delete-icon {
  cursor: pointer;
}
 